'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Separator } from '@/components/ui/separator';
import { toast } from 'sonner';
import { Navbar } from '@/components/layout/navbar';
import { Footer } from '@/components/layout/footer';
import { useAuth } from '@/lib/auth/context';
import { riderStorage, rideStorage, safetyGearStorage } from '@/lib/data/storage';
import { Rider } from '@/lib/types/rider';
import { Ride } from '@/lib/types/ride';
import { SafetyGear } from '@/lib/types/safety';

export default function AdminPage() {
  const router = useRouter();
  const { currentUser } = useAuth();
  const [riders, setRiders] = useState<Rider[]>([]);
  const [rides, setRides] = useState<Ride[]>([]);
  const [safetyGear, setSafetyGear] = useState<SafetyGear[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedTab, setSelectedTab] = useState('overview');

  useEffect(() => {
    // For demo purposes, allow access to admin page
    // In production, you'd check for admin role
    loadData();
  }, []);

  const loadData = () => {
    setRiders(riderStorage.getAll());
    setRides(rideStorage.getAll());
    setSafetyGear(safetyGearStorage.getAll());
  };

  const filteredRiders = riders.filter(rider =>
    rider.firstName.toLowerCase().includes(searchTerm.toLowerCase()) ||
    rider.lastName.toLowerCase().includes(searchTerm.toLowerCase()) ||
    rider.email.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const stats = {
    totalRiders: riders.length,
    activeRiders: riders.filter(r => r.isActive).length,
    totalRides: rides.length,
    upcomingRides: rides.filter(r => new Date(r.date) >= new Date() && r.status === 'Planned').length,
    completedRides: rides.filter(r => r.status === 'Completed').length,
    safetyGearItems: safetyGear.length,
  };

  const toggleRiderStatus = (riderId: string) => {
    const rider = riders.find(r => r.id === riderId);
    if (rider) {
      const updatedRider = { ...rider, isActive: !rider.isActive };
      riderStorage.update(updatedRider);
      loadData();
      toast.success(`Rider ${rider.isActive ? 'deactivated' : 'activated'} successfully`);
    }
  };

  const deleteRider = (riderId: string) => {
    const updatedRiders = riders.filter(r => r.id !== riderId);
    riderStorage.save(updatedRiders);
    loadData();
    toast.success('Rider deleted successfully');
  };

  const cancelRide = (rideId: string) => {
    const ride = rides.find(r => r.id === rideId);
    if (ride) {
      const updatedRide = { ...ride, status: 'Cancelled' as const };
      rideStorage.update(updatedRide);
      loadData();
      toast.success('Ride cancelled successfully');
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <Navbar />
      
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Admin Dashboard
          </h1>
          <p className="text-gray-600">
            Manage riders, rides, and safety protocols for BikeRide Community
          </p>
        </div>

        {/* Stats Overview */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
          <Card>
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">Total Riders</p>
                  <p className="text-2xl font-bold text-gray-900">{stats.totalRiders}</p>
                </div>
                <Badge variant="secondary">{stats.activeRiders} Active</Badge>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">Total Rides</p>
                  <p className="text-2xl font-bold text-gray-900">{stats.totalRides}</p>
                </div>
                <Badge variant="secondary">{stats.upcomingRides} Upcoming</Badge>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">Completed Rides</p>
                  <p className="text-2xl font-bold text-gray-900">{stats.completedRides}</p>
                </div>
                <Badge variant="secondary">Safety First</Badge>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Main Content */}
        <Tabs value={selectedTab} onValueChange={setSelectedTab} className="space-y-6">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="overview">Overview</TabsTrigger>
            <TabsTrigger value="riders">Riders</TabsTrigger>
            <TabsTrigger value="rides">Rides</TabsTrigger>
            <TabsTrigger value="safety">Safety</TabsTrigger>
          </TabsList>

          {/* Overview Tab */}
          <TabsContent value="overview" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Recent Riders</CardTitle>
                  <CardDescription>Latest community members</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {riders.slice(0, 5).map((rider) => (
                      <div key={rider.id} className="flex items-center justify-between">
                        <div>
                          <p className="font-medium">{rider.firstName} {rider.lastName}</p>
                          <p className="text-sm text-gray-500">{rider.email}</p>
                        </div>
                        <Badge variant={rider.isActive ? 'default' : 'secondary'}>
                          {rider.isActive ? 'Active' : 'Inactive'}
                        </Badge>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Upcoming Rides</CardTitle>
                  <CardDescription>Scheduled community rides</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {rides.filter(r => new Date(r.date) >= new Date()).slice(0, 5).map((ride) => (
                      <div key={ride.id} className="flex items-center justify-between">
                        <div>
                          <p className="font-medium">{ride.title}</p>
                          <p className="text-sm text-gray-500">
                            {new Date(ride.date).toLocaleDateString()} at {ride.startTime}
                          </p>
                        </div>
                        <Badge variant="outline">
                          {ride.participants.length}/{ride.maxParticipants}
                        </Badge>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Riders Tab */}
          <TabsContent value="riders" className="space-y-6">
            <Card>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle>Rider Management</CardTitle>
                    <CardDescription>Manage community members and their profiles</CardDescription>
                  </div>
                  <div className="flex items-center space-x-2">
                    <Label htmlFor="search">Search:</Label>
                    <Input
                      id="search"
                      placeholder="Search riders..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      className="w-64"
                    />
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Name</TableHead>
                      <TableHead>Email</TableHead>
                      <TableHead>Experience</TableHead>
                      <TableHead>Blood Group</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Actions</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {filteredRiders.map((rider) => (
                      <TableRow key={rider.id}>
                        <TableCell className="font-medium">
                          {rider.firstName} {rider.lastName}
                        </TableCell>
                        <TableCell>{rider.email}</TableCell>
                        <TableCell>{rider.experience}</TableCell>
                        <TableCell>{rider.bloodGroup}</TableCell>
                        <TableCell>
                          <Badge variant={rider.isActive ? 'default' : 'secondary'}>
                            {rider.isActive ? 'Active' : 'Inactive'}
                          </Badge>
                        </TableCell>
                        <TableCell>
                          <div className="flex space-x-2">
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => toggleRiderStatus(rider.id)}
                            >
                              {rider.isActive ? 'Deactivate' : 'Activate'}
                            </Button>
                            <Button
                              size="sm"
                              variant="destructive"
                              onClick={() => deleteRider(rider.id)}
                            >
                              Delete
                            </Button>
                          </div>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Rides Tab */}
          <TabsContent value="rides" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Ride Management</CardTitle>
                <CardDescription>Manage community rides and events</CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Title</TableHead>
                      <TableHead>Date</TableHead>
                      <TableHead>Organizer</TableHead>
                      <TableHead>Participants</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Actions</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {rides.map((ride) => {
                      const organizer = riders.find(r => r.id === ride.organizer);
                      return (
                        <TableRow key={ride.id}>
                          <TableCell className="font-medium">{ride.title}</TableCell>
                          <TableCell>
                            {new Date(ride.date).toLocaleDateString()} at {ride.startTime}
                          </TableCell>
                          <TableCell>
                            {organizer ? `${organizer.firstName} ${organizer.lastName}` : 'Unknown'}
                          </TableCell>
                          <TableCell>
                            {ride.participants.length}/{ride.maxParticipants}
                          </TableCell>
                          <TableCell>
                            <Badge variant={
                              ride.status === 'Completed' ? 'default' :
                              ride.status === 'Cancelled' ? 'destructive' :
                              ride.status === 'Active' ? 'secondary' : 'outline'
                            }>
                              {ride.status}
                            </Badge>
                          </TableCell>
                          <TableCell>
                            {ride.status === 'Planned' && (
                              <Button
                                size="sm"
                                variant="destructive"
                                onClick={() => cancelRide(ride.id)}
                              >
                                Cancel
                              </Button>
                            )}
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Safety Tab */}
          <TabsContent value="safety" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Safety Gear Management</CardTitle>
                <CardDescription>Manage required safety equipment and protocols</CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Item</TableHead>
                      <TableHead>Category</TableHead>
                      <TableHead>Required</TableHead>
                      <TableHead>Description</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {safetyGear.map((gear) => (
                      <TableRow key={gear.id}>
                        <TableCell className="font-medium">{gear.name}</TableCell>
                        <TableCell>{gear.category}</TableCell>
                        <TableCell>
                          <Badge variant={gear.required ? 'destructive' : 'secondary'}>
                            {gear.required ? 'Required' : 'Optional'}
                          </Badge>
                        </TableCell>
                        <TableCell>{gear.description}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Safety Statistics</CardTitle>
                <CardDescription>Community safety metrics and compliance</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <div className="text-center p-4 border rounded-lg">
                    <div className="text-2xl font-bold text-green-600">99.8%</div>
                    <div className="text-sm text-gray-600">Safety Compliance</div>
                  </div>
                  <div className="text-center p-4 border rounded-lg">
                    <div className="text-2xl font-bold text-blue-600">0</div>
                    <div className="text-sm text-gray-600">Safety Incidents</div>
                  </div>
                  <div className="text-center p-4 border rounded-lg">
                    <div className="text-2xl font-bold text-purple-600">{safetyGear.filter(g => g.required).length}</div>
                    <div className="text-sm text-gray-600">Required Gear Items</div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>

      <Footer />
    </div>
  );
}
